.ledgers-container {
  padding: 20px 0;
}

.ledger-header-container {
  display: flex;
  gap: 10px;
}

.center-align {
  text-align: center !important;
}

.ledger-product-listing-container {
  margin-top: 20px;
}

.ledgers-container .MuiButtonBase-root .MuiTableSortLabel-icon {
  color: lightgreen !important;
}

.ledger-product-listing-container ::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.ledger-product-listing-container ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #fff;
  border-radius: 10px;
}

.ledger-product-listing-container ::-webkit-scrollbar-thumb {
  background-color: lightgreen;
  border-radius: 10px;
}

.ledger-product-listing-container ::-webkit-scrollbar-thumb:hover {
  background-color: lightgreen;
}

.insights-list-container {
  background-color: #f8f9fa; /* Light grey background */
  border-radius: 8px; /* Rounded corners */
  padding: 20px; /* Padding around the text */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  font-family: "Arial", sans-serif; /* Modern font */
  color: #333; /* Dark grey text for readability */
  max-width: 400px; /* Maximum width of the container */
  margin: auto; /* Centering the container */
  list-style: none; /* Remove default list styling */
}

li {
  border-bottom: 1px solid #ddd; /* Separator between items */
  padding: 10px 0; /* Padding for each item */
  margin: 0 10px; /* Margin inside the list */
}

li:last-child {
  border-bottom: none; /* No border for the last item */
}

/* Adding a hover effect for interaction */
li:hover {
  background-color: #ececec;
}

/* Title style if needed separately */
.title {
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 20px;
}
